import React, { useState } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
  CFormFeedback,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { cilLockLocked, cilUser } from "@coreui/icons";
import { Link } from "react-router-dom";

import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";

import Message from "../../../components/Message";

import { registerUser } from "../../../provider/userFetch";

const schemaUser = yup.object().shape({
  firstName: yup.string().required("firstName is required"),
  lastName: yup.string().required("lastName is required"),
  email: yup
    .string()
    .email("email must email valid")
    .required("email is required"),
  password: yup.string().required("password are required"),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref("password"), null], "Passwords must match"),
});

const Register = () => {
  /** State */
  const [message, setMessage] = useState(null);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);

  /** Form Hook */
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting, isValid },
    resetField,
  } = useForm({
    resolver: yupResolver(schemaUser),
    mode: "onSubmit",
  });

  const onSubmit = async (data) => {
    let resp = null;

    if (isValid) {
      /** reste */
      setSuccess(false);
      setError(false);
      resp = await registerUser(data);
    }

    if (resp?.status) {
      /** Reset */
      resetField("email");
      resetField("password");

      setSuccess(true);
    } else {
      setError(true);
      setMessage(resp?.error);
    }
  };

  console.log("error", errors, isSubmitting, isValid);

  return (
    <div className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          {error && <Message> {message}</Message>}
          {success && (
            <Message>
              {`Success register`}

              <Link to="/login">
                <CButton color="primary" className="mt-3" active tabIndex={-1}>
                  Login Now!
                </CButton>
              </Link>
            </Message>
          )}
          <CCol md={9} lg={7} xl={6}>
            <CCard className="mx-4">
              <CCardBody className="p-4">
                <CForm
                  className={`row g-3 needs-validation ${
                    isValid && "was-validated"
                  }`}
                  onSubmit={handleSubmit(onSubmit)}
                >
                  <h1>Register</h1>
                  <p className="text-medium-emphasis">Create your account</p>
                  <CInputGroup className="mb-3">
                    <CInputGroupText>
                      <CIcon icon={cilUser} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder="firstName"
                      autoComplete="firstName"
                      {...register("firstName")}
                    />
                    <CFormFeedback invalid>
                      {errors.firstName?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupText>
                      <CIcon icon={cilUser} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder="lastName"
                      autoComplete="lastName"
                      {...register("lastName")}
                    />
                    <CFormFeedback invalid>
                      {errors.lastName?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupText>@</CInputGroupText>
                    <CFormInput
                      placeholder="Email"
                      autoComplete="email"
                      {...register("email")}
                    />
                    <CFormFeedback invalid>
                      {errors.email?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupText>
                      <CIcon icon={cilLockLocked} />
                    </CInputGroupText>
                    <CFormInput
                      type="password"
                      placeholder="Password"
                      autoComplete="new-password"
                      {...register("password")}
                    />
                    <CFormFeedback invalid>
                      {errors.password?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <CInputGroup className="mb-4">
                    <CInputGroupText>
                      <CIcon icon={cilLockLocked} />
                    </CInputGroupText>
                    <CFormInput
                      type="password"
                      placeholder="Repeat password"
                      autoComplete="new-password"
                      {...register("confirmPassword")}
                    />
                    <CFormFeedback invalid>
                      {errors.confirmPassword?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <div className="d-grid">
                    <CButton color="success" type="submit">
                      Create Account
                    </CButton>
                  </div>
                </CForm>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  );
};

export default Register;
